.NavBarItems{
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px  0 rgb(0,0,0,0.25);
    width: 96%;
    height: 80px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 9999;
}

.navbar{
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(6,auto );
    grid-gap:16px;
    list-style: none;
    align-items: center;

}
.nav-link{
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}
.nav-link i{
    padding: 10px;
}
.nav-link:hover{
    background-color: #01959a;
    color: #fff;
    border-radius:4px ;
    transition: all .2s ease-in-out;
}
.nav-link-mobile{
    display: none;

}
.button{
    padding: 0.5rem 1 rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: .2s ease-in-out;   
}

.button:hover{
    background-color: #151516;
    color: #fff;
    border-radius:4px ;
    transition: all .2s ease-in-out;
}
.menu-icons{
    display: none;
}
.closeNavbar{
display: none;
}

@media screen and (max-width:850px)
{
    .NavBarItems{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background:#fff;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 0;
        align-items: stretch;
        padding: 80px 0 30px 0;
        margin: 0;
        z-index: -1;
        transition: 0.3s ease-in-out;
        border-radius: 13px;
    }
    
    .nav-menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out;
    }

    .nav-link{
       display: block;
       width: 100%;
       font-size: 1.2rem;
       padding: 2rem 0 ;
    }
    .nav-link:hover{
        background-color: #01959a;
        transition: none;

    }
    .button{
       display: none;
    }
    .nav-link-mobile{
        display: block ;
        text-align: center;
        padding: 1.5rem;
        margin: auto;
        border-radius: 4px;
        width: 80%;
        background-color: #01959a;
        text-decoration: none;
        color: #fff;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
    }
    .menu-icons{
        display: block;
        font-size: 1.5rem;
       
    }
    .closeNavbar{
    display: block; 
    font-size: 1.5rem;
 
    }
    
}
